export default {
	data: () => ({
		dialogHandleValue: false,
        languageList :[
            {
                id:'en',
                title:'English'
            },
            {
                id:'zh',
                title:'中文'
            }
        ]
	}),
	components: {
	},
	mounted() {
	},
	methods: {
        open() {
            console.log("batchDialog");
            this.dialogHandleValue = true;
        },
        changeLanguage(type) {
			this.$i18n.locale = type;
			this.dialogHandleValue = false;
            this.$emit('closeDialog');
		},
	}
};
