import showOptions from "./showOptions.vue";
import { mapActions } from "vuex"
export default {
    data: () => ({
        i18nTypes: {
            en: "English",
            zh: "简体中文",
        },
    }),
    components: {
        showOptions,
    },
    methods: {
        ...mapActions({ 'setI18nType': 'setI18nType' }),
        select(item) {
            this.setI18nType(item)
            this.$i18n.locale = item;
        },
    },
};
