export default {
    data: () => ({
        show: false,
        timer: "",
    }),
    methods: {
        openSelectOptions() {
            this.show = true;
        },
        closeSelectOptions() {
            this.show = false;
        },
        mouseleaveToClose() {
            // console.log("mouseleaveToClose");
            if (!this.show) return;
            this.timer = setTimeout(() => {
                this.closeSelectOptions();
            }, 1000);
        },
        mouseeventToClearTimer() {
            // console.log("mouseeventToClearTimer");
            if (this.timer && this.show) {
                clearTimeout(this.timer);
                this.openSelectOptions()
            }
        },
    },
};
