import showOptions from "./showOptions.vue";
export default {
    components: {
        showOptions,
    },
    props: {
        options: {
            type: Array,
            default: () => []
        }
    },
    computed: {
        ethAddress() {
            return this.$store.getters.ethAddress || "Link Wallet";
        },
    },
    methods: {
        select(item) {
            this.$routerUtil.toPath(item.path)
        },
    }
}
